.notas-error {
    text-align: center;
    font-weight: bold;
    color: red;
}
table {
    text-transform: uppercase;
}

.nombre-nota {
    cursor: pointer;
}
.popover {
    &::after {
        position: absolute;
        display: block;
        width: 0;
        height: 0;
        border-color: transparent;
        border-style: solid;
        top: -10px;
        margin-left: -10px;
        border-bottom-color: #f7f7f7;
    }

    .popover-inner {
        .popover-header {
            padding: 8px 14px;
            margin-bottom: 0;
            font-size: 1rem;
            background-color: #f7f7f7;
            border-bottom: 1px solid #ebebeb;
            border-top-right-radius: calc(0.24rem - 1px);
            border-top-left-radius: calc(0.24rem - 1px);
        }
        .popover-body {
            padding: 9px 14px;
        }
    }
}

.accion {
    cursor: pointer;

    &:hover {
        border-color: #9ecaed;
        box-shadow: 0 0 10px #9ecaed;
    }

    &.disabled {
        background-color: #f3f3f3;
        opacity: 0.5;
        &:hover {
            cursor: not-allowed;
        }
        .action-icon {
            background-color: #fff;
        }
    }
    .action-icon {
        width: 48px;
        height: 48px;
        border-radius: 2px;
        margin-right: 0 !important;
        margin-bottom: 10px;
    }
    h5 {
        text-align: center;
    }
}

.fila-botones-accion {
    display: flex;
    justify-content: flex-end;
}